.navbar {
    background-color: #009bde;
}

.navbar-brand {
    margin-right: 0px;
}

.nav-item {
    font-size: 1.25rem;
}

.nav-link {
    color: #ffffff;
}

.nav-toggler-icon {
    outline: none;
}
.oleHeader {
    color: #495057;
    font-size: 0.9rem;
}

.oleHeader a {
    color: #495057;
}

.oleHeader p {
    margin-bottom: 0rem;
}

.oleLogo {
    width: auto;
    height: 85px;
}

.oleOrder {
    width: 60px;
    height: 60px;
    z-index: 10;
}

@media screen and (max-width : 768px){
    .oleLogo {
        width: auto;
        height: 60px;
    }

    .oleOrder {
        width: 40px;
        height: 40px;
        z-index: 10;
    }

    .oleHeader {
        display: none;
    }
}