.oleFooterBgImg {
  background-image: url(../assets/footer-bg-desktop.jpg);
  background-color: #009bde; /** Just in case image is not available */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 700px;
}

@media screen and (max-width: 1024px) {
  .oleFooterBgImg {
    background-image: url(../assets/footer-bg-tablet.jpg);
    background-color: #009bde; /** Just in case image is not available */
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: right;
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 640px) {
  .oleFooterBgImg {
    background-image: url(../assets/footer-bg-mobile.jpg);
    background-color: #009bde; /** Just in case image is not available */
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: right;
    width: 100%;
    height: auto;
  }      
}

.oleFooterLicence {
  align-items: center;
  text-align: center;
  color: #fff;
}

.oleFooterLicence a {
  color: #fff;
}
