/* Tweak the styles of the form fields so that the mobile browser does 
 * not zoom in on them on focus. We just need them to have a minimum 
 * font size of 16px to prevent the zoom.
 */
select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}

a, a:active, a:focus {
  outline: none;
}

/* Set the width of the input type file to prevent page on mobile from
 * overflowing and adding a scrollbar.
 */
input[type=file] {
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
