.oleSidebar {
    width: 400px;
    margin-top: 6px;
    background-color: #f8f9fa;
    overflow: visible;
}

.oleSidebar-Menu-Title {
    color: #ffff;
    background-color: #009bde;
    font-size: 1.15rem;
}

.oleSidebar .list-group-item {
    border-color: #009bde;
}

.oleContent {
    margin-left: 6px;
    margin-top: 6px;
    background-color: #f8f9fa;
    overflow: visible;
}

@media screen and (max-width : 768px){
    .oleSidebar {
        width: 300px;
        max-height: 500px;
        overflow-y: auto;
        transition: all .3s;
    }

    .oleSidebar-Menu-Title {
        font-size: 1rem;
        font-weight: bold;
    }
    
}